/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .edgtf-widget-title-holder {
	    position: relative;
        margin-bottom: 24px;
	    padding-bottom: 18px;

        .edgtf-widget-title {
            color: $widget-heading-color;
	        line-height: 1em;
	        margin-top: 0;
            margin-bottom: 10px;
        }
	    
	    &:after {
		    content: '';
		    position: absolute;
		    bottom: 0;
		    left: 0;
		    width: 50px;
		    height: 7px;
		    background: $first-main-color;
	    }

        .edgtf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            color: $default-heading-color;
            font-weight: 700;
            text-transform: uppercase;
            @include edgtfH6();

            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        margin:0 0 ($widget-bottom-margin - 2px);

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline-block;
	                vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;
                }
            }
        }
    }
	
	&.widget_archive,
	&.widget_categories {
		ul li {
			padding:9px 0;
			
			&:last-of-type {
				padding-bottom: 0;
			}
			
			a {
	            @include edgtfDefaultHeadingStyle();
				font-size: 17px;
				margin: 0;
				padding-right: 0;
                
                &:hover {
	                color: $first-main-color;
                }
			}
		}
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .edgtf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .edgtf-searchform {
        .input-holder {
            border-bottom: 4px solid #fff;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include edgtfTransition(color .2s ease-in-out);
            }

            input.search-field {
                padding: 0;
                width: calc(100% - #{$widget-search-form-height});
                font-size: 16px;

                @include edgtfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                color: $widget-text-color;
                cursor: pointer;
                font-size: 14px;
                width: $widget-search-form-height;
                text-align: left; //Inverse because of the flipping below
	            @include edgtfTransform(scaleX(-1));

                @include edgtfTransition(color .2s ease-in-out);

                &:hover {
                    color: #fff;
	                @include edgtfFontShadowHover()
                }

                span {
                    display: block;
                    left: 0;
                    line-height: 38px;
                    position: absolute;
                    top: 2px;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include edgtfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud
	
	&.widget_tag_cloud .edgtf-widget-title-holder {
		margin-bottom: 27px;
	}

    .tagcloud {
	    
        a {
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 16px !important;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0 3px 0 0;
	        
	        &:not(:last-of-type) {
		        &:after {
			        content: ',';
		        }
	        }

            &:hover {
                @include edgtfFontShadowHover();
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// Media: Image
	
	&.widget_media_image {

        margin: 0 0 71px;

        img {
            display: inline-block;
            vertical-align: middle;
        }
    }
}