@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 71px 0 24px;;
    }

    .edgtf-comment {

        &.edgtf-pingback-comment {

            .edgtf-comment-text {
                padding: 0;
            }
        }
    }

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 20px 0 0;
            padding: 15px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 23px;
        position: relative;
        padding-bottom: 29px;
        
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 50px;
            height: 7px;
            background: $first-main-color;
        }
        
        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 5px;
        left: 0;
        width: 110px;
        height: 110px;
        float: left;

        img {
            display: block;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 135px;
        min-height: 110px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 0 0 4px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            text-transform: capitalize;

            &:hover {
	            color: #fff;
	            @include edgtfFontShadowHover()
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            float: left;
            margin: 0;
            letter-spacing: .1em;
        }

        .edgtf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .edgtf-comment-form-inner {
        padding: 37px 0 78px;
    }

    .comment-reply-title {
        position: relative;
        margin: 0 0 28px;
        padding-bottom: 28px;
        
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 50px;
            height: 7px;
            background: $first-main-color;
        }
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {

            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include edgtfButtonDefaultStyle();
    @include edgtfButtonSize();
    @include edgtfButtonSolidColor();

    cursor: pointer;

    &:hover {
	    color: $second-main-color;
	    @include edgtfShadow();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 15px 0 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.edgtf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.edgtf-custom-cf-7-button {
    input {
        position: absolute !important;
        color: transparent !important; /* hide default text for cf-7 */

        left: 0;
        top: 0;
        width: 100% !important;
        height: 100% !important;
        border: none !important;
        background-color: transparent !important;
    }
}

div.wpcf7 .ajax-loader {
    background-repeat: no-repeat !important;
    position: absolute !important;
    right: 0;
}

.edgtf-contact-us-form {
    .edgtf-btn {
        margin-top: 29px;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */


/* ==========================================================================
   Contact Form 7 Custom begin
   ========================================================================== */


.edgtf-custom-contact-form {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    max-width: 1130px;
    margin: auto;


    .wpcf7-form-control.wpcf7-text {
        line-height: 22px;
        font-size: 18px;
        padding-left: 52px;
        padding-bottom: 32px;
        padding-top: 15px;
        border: none;
        background-color: transparent;
        margin: 0;
    }

    span.wpcf7-not-valid-tip {
        position: absolute;
    }

    > *:first-child {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        float: left;
        width: 71%;

        .edgtf-line {
            position: absolute;
            left: 0;
            bottom: 2px;
            width: 100%;
            height: 5px;
            background-color: #fff;
	        box-shadow: 0 0 10px 4px rgba(255,170,160,.31);
            @include edgtfTransition(all .3s .2s);
        }
        
        &:hover,
        &:focus {
            .edgtf-line {
                box-shadow: 0 0 36px 5px rgba(255,170,150,.51);
            }
        }
    }

    > *:last-child {
        width: 225px;
        float: right;
    }

    @include ipad-landscape {
        > *:first-child {
            width: 60%;
        }
        > *:last-child {
        }
    }

    @include ipad-portrait {
        > *:first-child {
            width: 50%;
        }
        > *:last-child {


        }
    }

    @include phone-landscape {
        > *:first-child {
            width: 100%;
            float: none;
            .wpcf7-text {
                padding-left: 10px;
                padding-right: 10px;
                text-align: center;
            }
        }
        > *:last-child {
            float: none;
            margin: 30px auto auto;
        }
    }

}

.cf7_custom_style_1 {
	position: relative;
	z-index: 1;
	
	textarea {
		height: 110px;
	}
	
	button {
		@include edgtfButtonDefaultStyle();
		margin-top: 6px;
		margin-left: 6px;
	}
}


/* ==========================================================================
   Contact Form 7 Custom end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.edgtf-slider-is-moving {
        .owl-item {
            .edgtf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.edgtf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }

    &.edgtf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        @include ipad-landscape {
            display: none;
        }

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include edgtfTransform(translateY(-50%));
            @include edgtfTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            .edgt-entropia-arrow-left,
            .edgt-entropia-arrow-right {
                display: inline-block;
                position: relative;
                width: 0;
                height: 0;
                border-style: solid;

                &:after {
                    content: '';
                    display: inline-block;
                    position: absolute;
                    margin-top: -30px;
                    top: 0;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    z-index: -1;
                }
            }

            .edgt-entropia-arrow-left {
                border-width: 30px 30px 30px 0;
                border-color: transparent #fff transparent transparent;

                &:after {
                    left: 6px;
                    border-width: 30px 30px 30px 0;
                    border-color: transparent $first-main-color transparent transparent;
                    @include edgtfTransition(left 0.15s ease-out);
                }
            }

            &:hover  .edgt-entropia-arrow-left {
                &:after {
                    left: 0;
                }
            }

            .edgt-entropia-arrow-right {
                border-width: 30px 0 30px 30px;
                border-color: transparent transparent transparent #fff;

                &:after {
                    right: 6px;
                    border-width: 30px 0 30px 30px;
                    border-color: transparent transparent transparent $first-main-color;
                    @include edgtfTransition(right 0.15s ease-out);
                }
            }

            &:hover  .edgt-entropia-arrow-right {
                &:after {
                    right: 0;
                }
            }
        }

        .owl-prev {
            left: 45px;
        }

        .owl-next {
            right: 45px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 65px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 9px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                background-color: $first-main-color;
                box-sizing: border-box;

                @include edgtfTransition(background-color .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $second-main-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include edgtfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .edgtf-wrapper {
        padding: 0 !important;
    }

    .edgtf-content {
        height: 100vh;
    }

    &.admin-bar {
        .edgtf-content {
            height: calc(100vh - 32px);
        }
    }

    .edgtf-content-inner {
        @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
        @include edgtfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .edgtf-404-title {
            margin: 0;
        }

        .edgtf-404-subtitle {
            margin: 35px 0 0;
        }

        .edgtf-404-text {
            margin: 10px 0 0;
        }

        .edgtf-btn {
            margin: 30px 0 0;

            &.edgtf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .edgtf-content {
            margin: 0 !important;
            height: auto;

            .edgtf-content-inner {
                height: calc(100vh - 70px);
            }

        }

        .edgtf-page-not-found {
            padding: 0 20% 30px;
        }

        &.admin-bar {
            .edgtf-content {
                min-height: calc(100vh - 102px);
            }
        }
    }

    @include ipad-portrait {
        .edgtf-page-not-found {
            padding: 20px 30px 50px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 50px;
	height: 50px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include edgtfTransition(opacity 0.3s ease);
    }
	
	.edgtf-btt-arrow-holder {
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
		top: -6px;
		left: -6px;
        text-align: center;
        background-color: #fff;
		z-index: 2;
		@include edgtfTransition(left .2s ease-in-out, top .2s ease-in-out, box-shadow .2s ease-in-out);
		
		@include edgtfShadowButton($second-main-color);

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            height: 0;
            width: 0;
	        top: 50%;
	        left: 50%;
            border-width: 0 15px 15px;
	        border-style: solid;
	        border-color: transparent transparent $second-main-color;
	        
	        @include edgtfTransform(translate(-50%, -50%))
        }
    }
	
	.edgtf-btt-base {
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		line-height: inherit;
		background: $first-main-color;
		z-index: 1;
		@include edgtfShadowButtonAfter($first-main-color);
	}
	
	.no-touch & {
		&:hover {
			.edgtf-btt-arrow-holder {
				top:0;
				left: 0;
				@include edgtfShadow(transparent);
			}
			
			.edgtf-btt-base {
				@include edgtfShadow($first-main-color);
			}
		}
	}
	.touch & {
		&:active {
			.edgtf-btt-arrow-holder {
				top:0;
				left: 0;
				@include edgtfShadow(transparent);
			}
			
			.edgtf-btt-base {
				@include edgtfShadow($first-main-color);
			}
		}
	}
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

