.page {
	--color-text: #fff;
	--color-bg: #000;
	--gap-horizontal: 5px;
	--gap-vertical: 25px;
	--time-anim: 4s;
	--delay-anim: 2s;
	--blend-mode-1: none;
	--blend-mode-2: none;
	--blend-mode-3: hard-light;
	--blend-mode-4: overlay;
	--blend-mode-5: overlay;
	--blend-color-1: transparent;
	--blend-color-2: #52f1cd;
	--blend-color-3: #525df1;
	--blend-color-4: #f19b52;
	--blend-color-5: #af4949;
}

/* Glitch styles */
.edgtf-glitch, .edgtf-testimonial-image-holder {
	figure {
		position: relative;
		overflow: hidden;
	}
	
	img {
		position: absolute;
		top: 0;
		left: 0;
		transform: translate3d(0,0,0);
	
		&:first-child {
			position: relative;
			left: 0;
		}
	}
	
	@supports(-webkit-clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%)) or (clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%)) {
		/* Non-IE styles here */
		
		img:nth-child(n+2) {
			opacity: 0;
		}
		
		img:nth-child(n+2) {
			animation-duration: var(--time-anim);
			animation-delay: var(--delay-anim);
			animation-timing-function: linear;
			animation-iteration-count: infinite;
		}
		
		img:nth-child(2) {
			animation-name: glitch-anim-flash;
		}
		
		img:nth-child(3) {
			animation-name: glitch-anim-1;
			mix-blend-mode: hard-light;
		}
	}
}

.edgtf-glitch-txt {
	position: relative;
	left: auto;
	top: auto;
	animation-duration: 2s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	
	.edgtf-glitch-after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1121;
		color: black;
		mix-blend-mode: multiply;
		animation-name: glitch-anim-text;
		animation-name: glitch-anim-text-overlay;
		animation-delay: .3s;
		animation-duration: 1.2s;
		animation-timing-function: linear;
		animation-iteration-count: infinite;
	}
}


@supports(-webkit-clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%)) or (clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%)) {
	/* Animations */
	
	@keyframes glitch-anim-1 {
		0% {
			opacity: 1;
			transform: translate3d(var(--gap-horizontal),0,0);
			-webkit-clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
			clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
		}
		2% {
			-webkit-clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
			clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
		}
		4% {
			-webkit-clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
			clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
		}
		6% {
			-webkit-clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
			clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
		}
		8% {
			-webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
			clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
		}
		10% {
			-webkit-clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
			clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
		}
		12% {
			-webkit-clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
			clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
		}
		14% {
			-webkit-clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
			clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
		}
		16% {
			-webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
			clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
		}
		//vert
		16.1% {
			-webkit-clip-path: polygon(43% 0, 45% 0, 43% 100%,  45% 100%);
			clip-path: polygon(43% 0, 45% 0, 43% 100%,  45% 100%);
		}
		17.9% {
			-webkit-clip-path: polygon(64% 0, 64% 0, 64% 100%,  64% 100%);
			clip-path: polygon(64% 0, 64% 0, 64% 100%,  64% 100%);
		}
		//vert end
		18% {
			-webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
			clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
		}
		20% {
			-webkit-clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
			clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
		}
		21.9% {
			opacity: 1;
			transform: translate3d(var(--gap-horizontal),0,0);
		}
		22%, 100% {
			opacity: 0;
			transform: translate3d(0,0,0);
			-webkit-clip-path: polygon(0 0, 0 0, 0 0, 0 0);
			clip-path: polygon(0 0, 0 0, 0 0, 0 0);
		}
	}
	
	@keyframes glitch-anim-text {
		0%,15% {
			transform: translate3d(0,0,0);
		}
		16% {
			transform: translate3d(-2px,0,0);
		}
		18%,34% {
			transform: translate3d(0,0,0);
		}
		36% {
			transform: translate3d(2px,-2px,0);
		}
		38% {
			transform: translate3d(0,0,0);
		}
		49% {
			 transform: translate3d(-1px,1px,0);
		}
		50%,68% {
			mix-blend-mode: hard-light;
			transform: translate3d(0,0,0);
		}
		70.9% {
			transform: translate3d(1px,2px,0);
		}
		72%, 100% {
			transform: translate3d(0,0,0);
		}
	}
	
	@keyframes glitch-anim-text-overlay {
		0% {
			transform: translate3d(0,0,0);
			-webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0% 33%);
			clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0% 33%);
			opacity: 0;
		}
		4% {
			-webkit-clip-path: polygon(0 53%, 100% 53%, 100% 55%, 0 55%);
			clip-path: polygon(0 53%, 100% 53%, 100% 55%, 0 55%);
			opacity: 1;
		}
		8% {
			-webkit-clip-path: polygon(28% 1%, 26% 0, 25% 100%, 28% 100%);
			clip-path: polygon(28% 1%, 26% 0, 25% 100%, 28% 100%);
			opacity: 0;
		}
		12% {
			-webkit-clip-path: polygon(20% 0%, 25% 0, 25% 100%, 19% 100%);
			clip-path: polygon(20% 0%, 25% 0, 25% 100%, 19% 100%);
			transform: translate3d(-5px,0,0);
			opacity: 1;
		}
		18%,44% {
			-webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
			clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
			transform: translate3d(0,0,0);
		}
		46% {
			-webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
			clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
			transform: translate3d(2px,2px,0);
		}
		50% {
			-webkit-clip-path: polygon(69% 0, 79% 0, 79% 100%, 68% 100%);
			clip-path: polygon(69% 0, 79% 0, 79% 100%, 68% 100%);
			transform: translate3d(0,0,0);
		}
		54% {
			-webkit-clip-path: polygon(74% 0, 79% 0, 79% 100%, 74% 100%);
			clip-path: polygon(74% 0, 79% 0, 79% 100%, 74% 100%);
		}
		58% {
			-webkit-clip-path: polygon(0 78%, 100% 78%, 100% 80%, 0 80%);
			clip-path: polygon(0 78%, 100% 78%, 100% 80%, 0 80%);
			mix-blend-mode: hard-light;
			transform: translate3d(0,0,0);
		}
		60.9% {
			transform: translate3d(1px,2px,0);
		}
		62%, 100% {
			transform: translate3d(0,0,0);
			opacity: 0;
		}
	}
	
	/* Flash */
	@keyframes glitch-anim-flash {
		0% {
			opacity: 0;
			transform: translate3d(0, 0, 0);
		}
		4% {
			opacity: 0;
			transform: translate3d(-20px, -10px, 0);
		}
		5%, 5.4% {
			opacity: 0.8;
			mix-blend-mode: hard-light;
			-webkit-filter: hue-rotate(90deg);
			filter: hue-rotate(90deg);
		}
		5.5%,68.5% {
			opacity: 0;
			transform: translate3d(0,0,0);
		}
		69%, 69.5% {
			opacity: 0.8;
			transform: translate3d(20px, -16px, 0);
			mix-blend-mode: hard-light;
			-webkit-filter: hue-rotate(90deg);
			filter: hue-rotate(90deg);
		}
		69.6%, 100% {
			opacity: 0;
			transform: translate3d(0, 0, 0);
		}
	}
}

